import React from 'react'
import PropTypes from 'prop-types';
// import './stylesheet.scss'
import {css,jsx} from '@emotion/core'
import styled from 'styled-components'

const Border = styled.p`
        width: 270px;
        height:330px;
        border:1px solid black;
`;

const CircleImg = styled.img`
        /* padding-top:30px; */
        border-radius:50%;
`;

const Description = styled.div`
    display:flex;
    flex-direction:column;
    align-items:center;
`;

const UlList = styled.ul`
    display:flex;
    justify-content:space-between;
    margin:0;
  
`;


const Li = styled.li`
    list-style:none;
`;
//Create Temp 
function Profiler({user}){
    return (
        <Border class="profile">
        <Description class="description">
          <CircleImg
            src={user.avatar}
            alt={user.name}
            class="avatar"
          />
          <p
          class="name">{user.name}</p>
          <p class="tag">@{user.tag}</p>
          <p class="location">{user.location}</p>
        </Description>
      
        <UlList class="stats">
          <Li>
            <span class="label">Followers: </span>
            <span class="quantity">{user.stats.followers}</span>
          </Li>
          <Li>
            <span class="label">Views: </span>
            <span class="quantity">{user.stats.views}</span>
          </Li>
          <Li>
            <span class="label">Likes: </span>
            <span class="quantity">{user.stats.likes}</span>
          </Li>
        </UlList>
      </Border>
    );
}

Profiler.defaultProps ={
        avatar: 'https://dummyimage.com/640x480/2a2a2a/ffffff&text=Product+image+placeholder'
}


Profiler.propTypes ={
  // to do prop type corect 
  user:PropTypes.shape({
    avatar:PropTypes.string.isRequired,
    name:PropTypes.string.isRequired,
    tag:PropTypes.string.isRequired,
    location:PropTypes.string.isRequired,
    stats:{
        followers:PropTypes.number.isRequired,
        views:PropTypes.number.isRequired,
        likes:PropTypes.number.isRequired,
    }   
  })
}

export default Profiler;